* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

body {
  width: 100%;
  min-height: 100vh;
}

@media (orientation: landscape) and (min-height: 550px) {
  body {
    overflow-x: hidden;
  }
}